.bgLogo {
  background-position: center;
  height: 10%;
  width: 40%;
  margin: auto;
  background-size: cover;
}

@media only screen and (max-width: 950px) {
  .bgLogo {
    width: 100%;
  }
}
html,
body,
section {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.background {
  background-image: linear-gradient(#000203 0%, #00345E 85%, #004D89 100%);
}

.illustration {
  z-index: 5;
  display: block;
  position: relative;
  width: auto;
  height: 90%;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}

.hover {
  width: 100%;
  height: 100%;
  animation: hover 5s ease-in-out infinite;
  animation-fill-mode: forwards;
}

.number-one {
  transform: translateY(-10px) translateZ(0);
  opacity: 0;
  animation: fadeNumberDown-10 2065ms ease-in-out infinite;
  animation-fill-mode: forwards;
}

